html {
  background-color: #000;
  overflow: hidden;
}

body {
  overflow: hidden;
  overscroll-behavior-y: none;
}

.c-main {
  padding: 0;
}

.c-home-link {
  left: 2rem;
  position: absolute;
  top: 1rem;
}

.scene {
  height: 100%;
  perspective: 600px;
  width: 100%;
}

.group {
  position: relative;
  transform-style: preserve-3d;
}

.plane {
  align-items: center;
  display: flex;
  flex-direction: column;
  left: 50vw;
  mix-blend-mode: screen;
  position: absolute;
  text-transform: uppercase;
  top: 50vh;
}

.plane--r {
  color: red;
  transform: rotateX(0) rotateY(0) translate3d(-50%, -50%, 0);
}

.plane--g {
  color: lime;
  transform: rotateX(0) rotateY(0) translate3d(-50%, -50%, 50px);
}

.plane--b {
  color: blue;
  transform: rotateX(0) rotateY(0) translate3d(-50%, -50%, 100px);
}

.fit-text {
  font-family: Poppins, sans-serif;
  font-size: calc(100vw / 5);
  font-weight: 400;
  line-height: 0.9;
}

.fit-text--b {
  transform: scaleX(1.215);
}

.fit-text--l {
  transform: scaleX(1.54);
}